import weui from 'weui.js'

"use strict";

/**
 * @description 
 * @param {string} url 
 */
function jumpLink(url) {
    if (url) {
        weui.loading('')
        if (url === 'BACK') {
            window.history.back();
        } else {
            window.location.href = url;
        }
    }
}

/**
 * @description 
 * @param {string} name 
 * @param {int} num
 */
function setTitle(name, num) {
    var titText = num === undefined ? name : (name + "( " + num + " )");
    var title = document.getElementsByTagName('title');

    function iframeLoad() {
        setTimeout(function () {
            iframeEl.removeEventListener('load', iframeLoad);
            iframeEl.remove();
        }, 0)
    }

    if (os().ios) {
        var bodyEl = document.body;
        var iframeEl = document.createElement('iframe');
        document.title = titText;
        iframeEl.src = '/Content/images/touming.png';
        iframeEl.onload = iframeLoad;
        bodyEl.appendChild(iframeEl);
        return
    }
    title[0].innerText = titText;
}

/**
 * @description 
 */
function os() {
    var u = window.navigator.userAgent;
    var android = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1; //
    var ios = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios
    var work = !!u.match(/wxwork/); //app
    return {
        android: android,
        ios: ios,
        work
    }
}

export default {
    jumpLink,
    setTitle
}


