import 'es6-promise/auto'
import FastClick from 'fastclick'
import lodashCore from 'lodash/core'
import weui from 'weui.js'
import Http from './http'
import Identity from './identity'
import Utils from './utils'
import Config from './config'
import listTemplae from './template/list.art'

//click
FastClick.attach(document.body);

const webapp = {appKey: 'mobile_approval', site: ''};

//http 
let http = new Http(webapp);

function getTodoTest() {
    // var url = 'http://localhost:63342/webpack.example/data/myapplication.data.json';
    // $axios.get(url).then(response => {
    //     console.log(response.data)
    // }).catch(err => {
    //     console.log(err)
    // })
    $app.execute('http://localhost:63342/webpack.example/data/todo.data.json', $config.listType.todo);
}

function generateTemplate() {
    var url = 'http://localhost:63342/webpack.example/data/.data.json';
    $axios.get(url).then(response => {
        let data = response.data;
        let htmlStr = listTemplae(data);
        let artEl = document.getElementById('art');
        console.log(artEl)
        artEl.innerHTML = htmlStr;
    }).catch(err => {
        console.log(err)
    })

}

/**
 * @description 
 * @param url {string} url
 * @param type {int} 
 */
function execute(url, type) {
    let title = "",
        loading = null,
        elId = "",
        el = null;
    switch (type) {
        case Config.listType.todo:
            title = "";
            elId = 'todo';
            el = document.getElementById(elId);
            break;
        case Config.listType.application:
            title = "";
            elId = 'application';
            el = document.getElementById(elId);
            break;
        case Config.listType.transaction:
            title = "";
            elId = 'transaction';
            el = document.getElementById(elId);
            break;
        case Config.listType.cc:
            title = "";
            elId = 'cc';
            el = document.getElementById(elId);
            break;
        default:
            break;
    }
    if (!el || !title) {
        weui.alert(` id=${elId}html `);
        return
    }
    loading = weui.loading('');
    $axios
        .get(url)
        .then(function (response) {
            loading.hide();
            let data = response.data;
            data.listType = type;//
            let html = listTemplae(data);
            el.innerHTML = html;
            Utils.setTitle(title, data.items.length);
        })
        .catch(function (err) {
            loading.hide();
            weui.alert(JSON.stringify(err));
        });
}

/**
 * @description 
 */
function showDoneMenu() {
    weui.actionSheet(
        [
            {
                label: '',
                onClick: function () {
                    $utils.jumpLink("/app/application");
                }
            },
            {
                label: '',
                onClick: function () {
                    $utils.jumpLink("/app/transaction");
                }
            }
        ],
        [{
            label: '',
            onClick: function () {
                console.log('');
            }
        }]
    );
}


window.$axios = http.$axios;
window.$authInstance = new Identity({webapp});
window.$utils = Utils;
window.$config = Config;
window.$_ = lodashCore;
window.$weui = weui;
window.$app = {
    execute,
    showDoneMenu
};

window.getTodoTest = getTodoTest;
window.generateTemplate = generateTemplate;



